/*@import url("https://fonts.googleapis.com/css?family=Roboto+Condensed");*/
@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes shake {
  0%,100% {
    transform: rotate(0deg);transform-origin: 50% 0;
  }
  10% {
    transform: rotate(2deg);
  }
  20%,40%,60% {
    transform: rotate(-4deg);
  }
  30%,50%,70% {
    transform: rotate(4deg);
  }
  80% {
    transform: rotate(-2deg);
  }
  90% {
    transform: rotate(2deg);
  }
}

* {
  margin: 0;
  font-family: 'Kanit', serif;
  /*max-width: 100vw;*/
}

.App{
  /*margin: 0;*/
  background-image: linear-gradient(#6596a9, #72a9be);
  /*width: 100vw;*/
}

nav{
  background: #2b6cb4;
  position: sticky;
  top: 0;
  display: flex;
  flex-direction: row;
  /*margin: 0;*/
  padding-right: 5rem;
  width: 100vw;
  justify-content: right;
  height: 5vh;
  font-size: 4vh;
  float: right;
  font-family: 'Roboto Condensed', sans-serif;
}

a{
  text-decoration: none;
  color: white;
}

a:hover{
  color: #6596a9;
}

nav li {
  /*margin: 0;*/
  padding-left: 2rem;
  list-style-type: none;
}

img {
  width: 30vw;
  height: 30vh;
}

#welcome-section{
  /*margin: 0;*/
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /*width: 100vw;*/
  height: 100vh;
  background-image: linear-gradient(#2b6cb4, rgba(113, 154, 169, 0));
  font-family: 'Kanit', serif;
  color: white;
}

#welcome-wrapper{
  animation: fadeIn 5s;
  display: flex;
  flex-direction: column;
  align-items: flex-end;

}

#name{
  display: flex;
  flex-direction: row;
  font-size: 5rem;
}
#emoji{
  animation: shake 4s ease-in 0s alternate none;
}
#flavor{
  font-size: 1.5rem;
}

#about-section{
  animation: fadeIn 5s;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}

#about-body{
  padding-left: 20vw;
  padding-right: 20vw;
  padding-top: 10vh;
}

h2{
  font-family: 'Kanit', serif;
  color: white;
  font-size: 3rem;
}

#projects{
  padding-top: 3rem;
}

#projects-header{
  padding-bottom: 3rem;
}

.projects-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.projects-grid > div{
  padding: 1rem;
}

#contact{
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: radial-gradient(circle at bottom, rgba(255, 255, 255, 0.25) 40vw, transparent 17.1vw)
}

#contact i {
  padding-right: 0.5rem;
}

#contact-wrapper{
  display: flex;
  flex-direction: column;
}

#contact-links{
  padding-top: 1rem;
  font-size: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

#top{
  padding-top: 3rem;
  font-size: 4rem;
  color: white;
}


#credits{
  padding-top: 3rem;

}

footer{
  margin: 0;
  background: #2b6cb4;
  display: flex;
  justify-content: flex-end;
  padding-right: 5rem;
  font-size: 1rem;
  height: 200%;
}
